.about {

  background-image: url(../../img/bg-about.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  padding: var(--offset-padding) 0;

  @media (max-width:650px) {
    & {
      padding: var(--offset-padding-m) 0;
    }
  }

  &__wrap {
    display: flex;
    align-items: flex-start;
    gap: 50px;

    @media (max-width:1200px) {
      & {
        flex-direction: column;
      }
    }
  }

  &__media{
    display: flex;
    gap: 20px;
    margin-top: 40px;

    @media (max-width:700px) {
      flex-direction: column;
      max-width: 500px;
      width: 100%;
      margin: 0 auto;
          margin-top: 40px;
      gap: 40px;
    }
  }

  &__certificat{
    max-width: 300px;
    margin: 0 auto;
  }

  &__video{
    width: 100%;
    border-radius: 10px;

        @media (max-width:700px) {
          aspect-ratio: 3/2;
    }
  }

  &__left {
    width: 50%;

    @media (max-width:1200px) {
      & {
        max-width: 750px;
        width: 100%;
        margin: 0 auto;
        margin-top: 20px !important;
      }
    }
  }

  &__right {
    width: 50%;

    @media (max-width:1200px) {
      & {
        max-width: 750px;
        width: 100%;
        margin: 0 auto;
      }
    }
  }

  &__descr {
    color: #fff;
    font-size: 16px;
    margin: 10px 0;
    font-family: "Montserrat", sans-serif;
    font-weight: 400;
  }

  &__descr-2 {
    color: #DADADA;
    font-size: 16px;
    margin-bottom: 30px;
  }

  &__info {
    gap: 130px;
    display: flex;
    align-items: flex-start;
    margin-bottom: 40px;

    @media (max-width:900px) {
      & {
        gap: 70px;

      }
    }

    @media (max-width:650px) {
      & {
        flex-direction: column;
        gap: 30px;
      }
    }
  }

  &__info-title {
    font-size: 16px;
    color: #fff;
    margin-bottom: 12px;
    display: block;
  }

  &__info-number &__info-descr {
    display: block;
    color: #fff;
    font-size: 30px;
    font-weight: 600;

    @media (max-width:900px) {
      & {
        font-size: 25px;
      }
    }
  }

  &__info-adress &__info-descr {
    display: flex;
    align-items: flex-start;
    gap: 10px;

    & span {
      color: #fff;
      font-size: 18px;
    }
  }

  &__certificate {
    display: flex;
    align-items: center;
    gap: 60px;
    padding: 25px 20px;
    border-radius: 20px;
    background-color: rgba(255, 255, 255, 0.05);
    transition: 0.05s ease-in all;

    &:hover {
      background-color: rgba(255, 255, 255, 0.1);
      transform: scale(1.01);
      transition: 0.05s ease-in all;
    }
  }

  &__certificate-title {
    display: flex;
    gap: 10px;
    align-items: center;
  }

  &__certificate-title {

    & span {
      display: block;
      color: #fff;
      font-size: 22px;
      font-weight: 600;
      text-align: left;

      @media (max-width:900px) {
        & {
          font-size: 20px;
        }
      }
    }

    & img {
      width: 20px;
      height: auto;
    }
  }

  &__certificate-descr {
    text-align: left;
    margin-top: 10px;
    color: #B1B5C3;
    font-size: 14px;
    font-weight: 300;
    padding-left: 31px;
  }

  &__btn {
    margin: 0 auto;
    margin-top: 60px;
  }
}

.offer {

  &__top {
    display: flex;
    align-items: center;
    gap: 35px;
    max-width: 820px;
    justify-content: center;
    margin: 0 auto;

    @media (max-width:1200px) {
      & {
        gap: 25px;
        margin-top: 40px;
      }
    }

    & img {
      width: 160px;
      height: auto;

      @media (max-width:1200px) {
        & {
          width: 135px;
        }
      }

      @media (max-width:800px) {
        & {
          width: 115px;
        }
      }
    }
  }

  &__title {
    font-weight: 500;

    & b {
      color: #0BCACA;
    }
  }

  &__descr {
    font-size: 16px;
    color: #fff;
    text-align: center;
    max-width: 1000px;
    margin: 15px auto 50px auto;
    font-weight: 400;
  }

  &__wrap {
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    row-gap: 20px;
    column-gap: 50px;

    @media (max-width:1000px) {
      & {
        grid-template-columns: repeat(1, 1fr);
      }
    }
  }

  &__right {
    @media (max-width:1000px) {
      & {
        order: 3;
      }
    }
  }

  &__wrap-right {
    @media (max-width:1000px) {
      & {
        order: 4;
      }
    }
  }

  &__subtitle {
    font-size: 30px;
    color: #fff;
    text-align: center;
    font-weight: 500;

    @media (max-width:1200px) {
      & {
        font-size: 27px;
      }
    }
  }

  &__list {
    padding: 50px 35px;
    background-color: rgba(255, 255, 255, 0.05);
    border-radius: 30px;
    color: #FFF;

    @media (max-width:1100px) {
      & {
        padding: 35px 30px;
      }
    }
  }

  &__item {
    display: flex;
    align-items: flex-start;
    gap: 10px;

    &:not(:last-child) {
      margin-bottom: 10px;
    }
  }

  &__check {
    width: 22px;
    height: auto;
  }

  &__text {
    font-size: 18px;
    color: #F0F0F0;
    font-weight: 400;

    @media (max-width:1200px) {
      & {
        font-size: 17px;
      }
    }
  }

  &__btn {
    margin: 0 auto;
    margin-top: 60px;
  }
}
