.levels {
  padding: var(--offset-padding) 0;
  background-image: url(../../img/bg-levels.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;

  @media (max-width:650px) {
    & {
      padding: var(--offset-padding-m) 0;
    }
  }

  &__subtitle {
    text-align: center;
    margin-top: 10px;
  }

  &__title {
    text-align: center;
  }

  &__descr {
    color: #fff;
    font-size: 18px;
    text-align: center;
    margin-top: 10px;

    @media (max-width:800px) {
      & {
        font-size: 16px;
      }
    }
  }

  &__list {
    list-style-type: none;
    display: grid;
    grid-template-columns: repeat(2, 1fr);
    gap: 50px;
    margin: 0;
    padding: 0;
    margin-top: 50px;

    @media (max-width:950px) {
      & {
        display: flex;
        flex-direction: column;
        align-items: center;
      }
    }
  }

  &-vip {
    background-image: url(../../img/bg-vip.svg);
    background-size: cover;
    background-repeat: no-repeat;
    background-position: center;
  }

  &__gold {
    background-color: #182932;
  }

  &__item {
    color: #fff;
    padding: 30px;
    border-radius: 20px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    gap: 20px;

    @media (max-width:950px) {
      & {
        max-width: 600px;
        width: 100%;
      }
    }
  }

  &__top {
    display: flex;
    align-items: center;
    gap: 20px;
  }

  &__head {
    font-size: 26px;
    font-family: "Raleway", sans-serif;
    font-weight: 600;

    @media (max-width:1200px) {
      & {
        font-size: 24px;
      }
    }

                          @media (max-width:650px) {
      & {
        font-size: 24px;
      }
    }
  }

  &__subtext {
    font-weight: 300;
    font-size: 16px;
    margin-top: 5px;
  }

  &__price-wrap {
    display: flex;
    gap: 5px;
    align-items: flex-end;
  }

  &__month {
    font-weight: 300;
    font-size: 16px;
  }

  &__price {
    font-size: 40px;
    font-weight: 500;
    font-family: "Outfit", sans-serif;

    @media (max-width:1200px) {
      & {
        font-size: 37px;
      }
    }

    @media (max-width:650px) {
      & {
        font-size: 32px;
      }
    }
  }

  &__sublist {
    margin: 0;
    padding: 0;
    margin-top: 16px;
    list-style-type: none;
    display: flex;
    flex-direction: column;
    gap: 16px;
  }

  &__subitem {
    display: flex;
    align-items: flex-start;
    gap: 11px;
  }

  &__header {
    font-size: 18px;
    font-family: "Raleway", sans-serif;
    font-weight: 500;

                      @media (max-width:650px) {
      & {
        font-size: 16px;
      }
    }
  }

  &__subdescr {
    font-weight: 300;
    font-family: "Raleway", sans-serif;
    font-size: 16px;
    margin-top: 5px;

                          @media (max-width:650px) {
      & {
        font-size: 14px;
      }
    }
  }

  &__btn {
    font-weight: 500;
    font-size: 20px;
    font-family: "Raleway", sans-serif;
    color: #fff;
    display: block;
    width: 100%;
    text-align: center;
    border-radius: 8px;
    border: 0.7px solid #fff;
    padding: 15px 0;
    background-color: #181E2A;
    transition: 0.05s ease-in all;

    @media (max-width:1200px) {
      & {
        font-size: 18px;
      }
    }

    @media (max-width:1100px) {
      & {
        padding: 12px 0;
      }
    }

                  @media (max-width:650px) {
      & {
        font-size: 16px;
      }
    }

    &:hover {
      background-color: #2c3550;
      box-shadow: 0 4px 12px rgba(255, 255, 255, 0.3);
      transform: translateY(-3px);
      transition: 0.05s ease-in all;
    }

    &:active {
      transform: translateY(0);
      box-shadow: none;
      transition: 0.05s ease-in all;
    }
  }

  &-vip &__btn {
    background-color: #fff;
    color: #196264;
  }

  &__icon {
    @media (max-width:1200px) {
      & {
        width: 90px;
      }
    }

    @media (max-width:650px) {
      & {
        width: 80px;
      }
    }
  }
}
