.brands {

  padding: var(--offset-padding) 0;
  background-image: url(../../img/bg-brands.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  position: relative;

  @media (max-width:650px) {
    & {
      padding: var(--offset-padding-m) 0;
    }
  }

  &__container {
    display: flex;
    align-items: center;
    gap: 50px;

    @media (max-width:1200px) {
      & {
        flex-direction: column;
      }
    }
  }

  &__left {
    width: 50%;

    @media (max-width:1200px) {
      & {
        width: 100%;
      }
    }
  }

  &__right {
    width: 50%;
    position: relative;
    z-index: 99;

    @media (max-width:1200px) {
      & {
        width: 100%;
      }
    }
  }

  &__title {
    position: relative;
    z-index: 100;
  }

  &__title b {
    color: #0EFFFF;
  }

  &__descr {
    color: #fff;
    font-size: 18px;
    margin-top: 10px;
    position: relative;
    z-index: 100;
    font-weight: 400;

    @media (max-width:800px) {
      & {
        font-size: 16px;
      }
    }
  }

  &__list {
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    column-gap: 15px;
    row-gap: 30px;

    @media (max-width:550px) {
      & {
        grid-template-columns: repeat(5, 1fr);
      }
    }
  }

  &__element {
    opacity: 0.5;
    position: absolute;
    left: 0;
    top: 50%;
    transform: translateY(-50%);
  }

  &__img {
    width: 55px;
    height: auto;
  }

  &__item {
    display: flex;
    align-items: center;
    justify-content: center;
  }
}
