.footer {
  background-image: url(../../img/bg-footer.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: center;
  padding: 80px 0 20px 0;

    @media (max-width:650px) {
    & {
     padding: 60px 0 20px 0;
    }
  }

  &__wrap {
    display: flex;
    align-items: flex-start;
    gap: 60px;
    justify-content: space-between;

    @media (max-width:1200px) {
      & {
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        gap: 30px;
      }
    }

    @media (max-width:850px) {
      & {
        grid-template-columns: repeat(1, 1fr);
      }
    }
  }

  &__company {
    @media (max-width:1200px) {
      & {
        order: 3;
      }
    }
  }

  &__social-item img {
    transition: 0.05s ease-in all;
    width: 35px;
    height: auto;
  }

  &__social-item:hover img {
    transform: scale(1.05);
    transition: 0.05s ease-in all;
  }

  &__logo {
    display: block;
    margin-bottom: 15px;

    & img {
      width: 130px;
      height: auto;
      display: block;
      object-fit: contain;
    }
  }

  &__descr {
    color: #E6E6E6;
    font-size: 16px;
    margin-bottom: 20px;
  }

  &__social {
    display: flex;
    align-items: center;
    gap: 10px;
    margin-bottom: 25px;
  }

  &__verification {
    margin-top: 10px;
    width: max-content;
    display: block;
    color: #0BCACA;
    border-bottom: 1px dotted #0BCACA;
    font-size: 17x;
    transition: 0.05s ease-in all;

    &:hover {
      color: #fff;
      border-bottom: 1px dotted #fff;
      transition: 0.05s ease-in all;
    }
  }

  &__title {
    font-size: 18px;
    color: #fff;
    margin-bottom: 15px;
    text-transform: uppercase;

            @media (max-width: 650px) {
      & {
        font-size: 17px;
        margin-bottom: 10px;
      }
    }
  }

  &__list {
    display: flex;
    flex-direction: column;
    gap: 7px;
  }

  &__link {
    display: block;
    font-size: 16px;
    color: #CECFD2;

    &:hover {
      color: #fff;
      transition: 0.03s ease-in all;
    }
  }

  &__btn {
    margin-left: auto;
    font-size: 14px;
    padding: 10px 30px;

    @media (max-width:1200px) {
      & {
        margin-left: 0;
      }
    }

    & img {
      width: 21px;
      height: 21px;
    }
  }

  &__contacts {
    display: flex;
    flex-direction: column;
    gap: 10px;
    align-items: flex-end;
    margin-top: 30px;

    @media (max-width:1200px) {
      & {
        align-items: flex-start;
      }
    }
  }

  &__contacts-item {
    display: flex;
    gap: 7px;
    align-items: flex-start;

    &__img {
      width: 20px;
      height: auto;
    }
  }

  &__contacts-text {
    color: #fff;
    font-size: 18px;

        @media (max-width: 650px) {
      & {
        font-size: 17px;
      }
    }
  }

  &__contacts-item:last-child span {
    min-width: 230px;
    text-align: right;

    @media (max-width:1200px) {
      & {
        text-align: left;
      }
    }
  }

  &__copyright {
    color: #fff;
    font-size: 16px;
    display: block;
    text-align: center;
    margin-top: 30px;
  }

  &__bottom {
    margin-top: 20px;
  }

  &__govern {
    width: 170px;
  }
}
