.header {

  &__container {
    position: fixed;
    z-index: 999;
    top: 15px;
    left: 50%;
    transform: translateX(-50%);
    max-width: 1440px;
    padding: 10px 15px 10px 15px;
    width: 100%;
    display: flex;
    gap: 30px;
    align-items: center;
    justify-content: space-between;
    border: 1px solid #37393C;
    box-shadow: 0 1px 4px 0 rgba(38, 40, 46, 0.37);
    border-radius: 60px;
    background: rgba(14, 59, 68, 0.8);
    backdrop-filter: blur(4px);
    -webkit-backdrop-filter: blur(4px);
  }

  &__top {
    display: flex;
    gap: 20px;
    align-items: center;
    justify-content: space-between;
    width: 100%;
  }

  &__reg {
    font-size: 14px;
  }

  &__nav {
    display: flex;
    list-style-type: none;
    gap: 30px;
  }

  @media (max-width:1250px) {
    &__nav {
      gap: 20px;
    }
  }

  &__nav-link {
    color: var(--text-color);
    font-size: 16px;
    font-family: "Raleway", sans-serif;
    font-weight: 400;
    transition: 0.05s ease-in all;

    &:hover {
      color: #fff;
      transition: 0.05s ease-in all;
    }
  }

  &__right {
    display: flex;
    align-items: center;
    gap: 22px;
  }

  &__btns {
    display: flex;
    align-items: center;
    gap: 22px;
  }

  &__login {
    padding: 13px 43px;
    font-size: 14px;
    color: var(--text-white-0);
    border: 2px solid rgba(255, 255, 255, 0.21);
    border-radius: 40px;
    box-shadow: 0 0 2px 0 rgba(29, 255, 255, 0.55);
    font-family: "Raleway", sans-serif;
    font-weight: 400;
    transition: 0.05 ease-in all;

    &:hover {
      color: #1cfffd;
      border-color: #1cfffd;
      box-shadow: 0 0 8px 2px #1cfffd;
      transform: scale(1.05);
      transition: 0.05 ease-in all;
    }

    @media (max-width:1200px) {
      & {
        padding: 8px 16px;
      }
    }
  }

  &__logo {

    & img {
      height: 40px;
      width: auto;
      object-fit: contain;
    }
  }

  @media (max-width:1200px) {
    &__reg {
      padding: 8px 16px;
    }
  }

  @media (max-width:1100px) {
    &__top {
      display: none;
    }

    .burger {
      z-index: 999;
      --burger-width: 30px;
      --burger-height: 20px;
      --burger-line-height: 3px;

      position: relative;
      border: none;
      padding: 0;
      width: var(--burger-width);
      height: var(--burger-height);
      color: #fff;
      background-color: transparent;
      cursor: pointer;

      &::before,
      &::after {
        content: "";
        position: absolute;
        left: 0;
        width: 100%;
        height: var(--burger-line-height);
        background-color: currentColor;
        transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
      }

      &::before {
        top: 0;
      }

      &::after {
        top: calc(100% - var(--burger-line-height));
      }

      &__line {
        position: absolute;
        left: 0;
        top: 50%;
        width: 100%;
        height: var(--burger-line-height);
        background-color: currentColor;
        transform: translateY(-50%);
        transition: transform 0.3s ease-in-out;
      }

      &--active {
        &::before {
          top: 50%;
          transform: rotate(45deg);
          transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
        }

        &::after {
          top: 50%;
          transform: rotate(-45deg);
          transition: transform 0.3s ease-in-out, top 0.3s ease-in-out;
        }

        .burger__line {
          transform: scale(0);
          transition: transform 0.3s ease-in-out;
        }
      }
    }
  }
}

.menu-btn {
  color: #fff;
  display: flex;
  gap: 3px;
  align-items: center;
}

.menu-item {
  position: relative;
}

.menu-btn span {
  color: #fff;
  font-weight: 400;
  font-size: 12px;
  font-weight: 700;
  letter-spacing: 1.2px;
  text-transform: uppercase;
}

// .menu-btn--active {
//   color: var(--red-color);
// }

.dropdown {
  padding: 3px 9px;
  border-radius: 3px;
  background: #fff;
  position: absolute;
  top: 120%;
  left: -4px;
  opacity: 0;
  visibility: hidden;
  transition: opacity 0.3s ease-in-out, visibility 0.3s ease-in-out;
}

.dropdowm-link {
  color: #000;
  font-size: 12px;
  font-weight: 700;
  letter-spacing: 1.2px;
  text-transform: uppercase;
  text-align: left;
}

.dropdown--active {
  opacity: 1;
  visibility: visible;
}

.dropdowm-link:hover {
  color: var(--yellow-color);
}

@media (max-width:1100px) {

  .header__top {
    position: absolute;
    top: -20px;
    left: 0;
    width: 100%;
    height: calc(100vh + 20px);

    transform: translateX(-100%);
    transition: transform 0.5s ease-in;

    background-color: #0D1925;
    padding: 30px;
    display: flex;
    flex-direction: column;
    /* <-- сюда перенеси */
    gap: 30px;
    align-items: center;
    z-index: 100;
  }

  .header__nav {
    flex-direction: column;
    align-items: center;
  }
}

.header__top--active {
  transform: translateX(0);
}

.header__top--active .header__nav {
  flex-direction: column;
  align-items: center;
}

.header__top--active .dropdown {
  top: -480%;
}

.header__top--active .menu-btn img {
  transform: scaleY(-1);
}
